import { Component } from 'react';
import './App.scss';
import { Carousel } from './components/Carousel';

interface State {
  images: string[];
  itemWidth: number;
  frameSize: number;
  step: number;
  animationDuration: number;
  infinite: boolean;
}

class App extends Component<{}, State> {
  state = {
    images: [
      './img/1.png',
      './img/2.png',
      './img/3.png',
      './img/4.png',
      './img/5.png',
      './img/6.png',
      './img/7.png',
      './img/8.png',
      './img/9.png',
      './img/10.png',
    ],
    step: 3,
    frameSize: 3,
    itemWidth: 130,
    animationDuration: 1000,
    infinite: false,
  };

  handleSubmit = (event: React.SyntheticEvent) => {
    event.preventDefault();
    const formElem = document.getElementById('formId') as HTMLFormElement;
    const form = new FormData(formElem);
    const data = Object.fromEntries(form.entries());

    this.setState({
      step: +data.step,
      frameSize: +data.frameSize,
      itemWidth: +data.itemWidth,
      animationDuration: +data.animationDuration,
      infinite: data.infinite !== 'false',
    });
  };

  render() {
    const {
      images,
      itemWidth,
      frameSize,
      step,
      animationDuration,
      infinite,
    } = this.state;

    return (
      <div className="App">
        {/* eslint-disable-next-line */}
        <h1 data-cy="title">
          {`Carousel with ${frameSize} images`}
        </h1>

        <Carousel
          images={images}
          step={step}
          frameSize={frameSize}
          itemWidth={itemWidth}
          animationDuration={animationDuration}
          infinite={infinite}
        />
        <form className="form" id="formId">
          <div className="form__container">
            <label htmlFor="itemId">
              <strong>Item width: </strong>
              <input
                type="number"
                name="itemWidth"
                defaultValue="130"
                id="itemId"
                className="form__input"
              />
            </label>

            <label htmlFor="frameId">
              <strong>Frame size </strong>
              <input
                type="number"
                name="frameSize"
                defaultValue="3"
                id="frameId"
                className="form__input"
              />
            </label>

            <label htmlFor="stepId">
              <strong>Step: </strong>
              <input
                type="number"
                name="step"
                defaultValue="3"
                id="stepId"
                className="form__input"
              />
            </label>

            <label>
              <strong>Animation duration: </strong>
              <input
                type="number"
                name="AnimationDuration"
                defaultValue="1000"
                className="form__input"
              />
            </label>

            <div className="form__radio-input">
              <strong>Infinite: </strong>
              <label>
                True
                <input
                  type="radio"
                  value="true"
                  name="infinite"
                />
              </label>
              <label>
                False
                <input
                  type="radio"
                  value="false"
                  name="infinite"
                  defaultChecked
                />
              </label>
            </div>
          </div>
          <div className="form__button">
            <button
              type="submit"
              onClick={this.handleSubmit}
            >
              Set values
            </button>
          </div>
        </form>
      </div>
    );
  }
}

export default App;
